<template>
    <div class="popover-content">
        <router-link to="/">
            <div class="popover-content__item">
                首页
            </div>
        </router-link>
        <div class="popover-content__item" @click.stop.prevent="menuClick('管理驾驶舱')">
            管理驾驶舱
        </div>
        <div class="popover-content__item" @click.stop.prevent="menuClick('辽宁沈阳410厂')">
            沈阳410厂
        </div>
        <div class="popover-content__item" @click.stop.prevent="menuClick('全屏')">
            {{fullscreen}}
        </div>
        <div class="popover-content__item qr" @click.stop.prevent="menuClick('手机版')">
            <div>手机版</div>
            <img style="width: 80px; height: 80px;" src="../../assets/image/qrcode.png" alt="" />
        </div>
    </div>
</template>
<script>
export default {
    name: 'menu-part',
    props: {},
    data() {
        return {}
    },
    computed: {
        fullscreen() {
            return this.isFullScreen() ? '退出全屏': '全屏'
        }
    },
    methods: {
        menuClick(eventName) {
            switch (eventName) {
                case '管理驾驶舱':
                    this.$router.push({ path: '/box/cockpit' })
                    location.reload()
                    break
                case '辽宁沈阳410厂':
                    this.$router.push({ path: '/box/cockpit?id=410' })
                    location.reload()
                    break
                case '全屏':
                    if (this.isFullScreen()) {
                        this.exitFullscreen()
                    } else {
                        this.launchFullScreen(document.documentElement)
                        // window.location.reload()
                    }
                    break
                default:
                    break
            }
        },
        launchFullScreen(ele) {
            if (ele.requestFullscreen) {
                ele.requestFullscreen()
            } else if (ele.mozRequestFullScreen) {
                ele.mozRequestFullScreen()
            } else if (ele.webkitRequestFullscreen) {
                ele.webkitRequestFullscreen()
            } else if (ele.msRequestFullscreen) {
                ele.msRequestFullscreen()
            }
        },
        exitFullscreen() {
            if (document.exitFullScreen) {
                document.exitFullScreen()
            } else if (document.mozCancelFullScreen) {
                document.mozCancelFullScreen()
            } else if (document.webkitExitFullscreen) {
                document.webkitExitFullscreen()
            } else if (document.msExitFullscreen) {
                document.msExitFullscreen()
            }
        },
        isFullScreen() {
            return !!(
                document.fullscreen ||
                document.mozFullScreen ||
                document.webkitIsFullScreen ||
                document.webkitFullScreen ||
                document.msFullScreen
            )
        },
    },
}
</script>
