<script>
import drawMixin from '../utils/drawMixin'
import Menu from '../components/menu'
import Title from '@/components/title'
import floatBtn from '../components/popmenu/floatBtn.vue'
import menuPart from '../components/popmenu/menuPart.vue'
export default {
    mixins: [drawMixin],
    data() {
        return {
            timing: null,
            loading: true,
            dateDay: null,
            dateYear: null,
            dateWeek: null,
            weekday: ['周日', '周一', '周二', '周三', '周四', '周五', '周六'],
            title: '中国航发司库管理运营平台',
        }
    },
    components: {
        Menu,
        Title,
        floatBtn,
        menuPart,
    },
    mounted() {
        console.log(this.$route.query.id)
        if (this.$route.query.id == 410) {
            this.title = '辽宁沈阳410厂'
        }
        this.cancelLoading()
    },
    beforeDestroy() {},
    methods: {
        closeMenuAndFloating() {
            this.$refs.floatBtn.closeMenuAndFloating()
        },
        cancelLoading() {
            setTimeout(() => {
                this.loading = false
            }, 500)
        },
    },
}
